import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Tabs, App } from 'ionic-angular';
import * as _ from 'lodash';

import { ShoppingcartPage } from '../../pages/shoppingcart/shoppingcart';
import { ChatPage } from '../../pages/chat/chat';
import { MyratingPage } from '../../pages/myrating/myrating';
import { AccountsettingsPage } from '../../pages/accountsettings/accountsettings';
import { MywalletPage } from '../../pages/mywallet/mywallet';
import { MypurchasesPage } from '../../pages/mypurchases/mypurchases';

import { LoginPage } from '../../pages/login/login';
import { SignupPage } from '../../pages/signup/signup';
import { AboutPage } from '../../pages/about/about';

import { TabsPage } from '../../pages/tabs/tabs';

@IonicPage()
@Component({
	selector: 'page-myprofile',
	templateUrl: 'myprofile.html',
})
export class MyprofilePage {
	imageUrl: string = 'assets/imgs/bg.jpg';
	user: any = {};
	mode: string = 'buyer';
	showProfile: boolean = false;
	constructor(public app: App, public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController) {
		console.log('ionViewCanEnter constructor');
		this.user = JSON.parse(localStorage.getItem('app.user')) || {};
		if (!_.isEmpty(this.user)) {
			this.showProfile = true;
		}
	}

	ionViewWillEnter() {
		console.log('ionViewWillEnter MyprofilePage');
		this.user = JSON.parse(localStorage.getItem('app.user')) || {};
		console.log('users: ', this.user);
		if (!_.isEmpty(this.user)) {
			this.showProfile = true;
		}
	}


	openLogin() {
		// TODO: Check for authentication
		let modal = this.modalCtrl.create(LoginPage);
		modal.onDidDismiss((res) => {
			if (res == 'login') {
				this.user = JSON.parse(localStorage.getItem('app.user')) || {};
				if (!_.isEmpty(this.user)) {
					this.showProfile = true;
				}
			}
		});
		modal.present();
	}

	openSignUp() {
		// TODO: Check for authentication
		console.log('openSignUp')
		let modal = this.modalCtrl.create(SignupPage);
		modal.present();
	}


	viewCart() {
		// TODO: Check if Authenticated
		// this.navCtrl.push(ShoppingcartPage);

		if (_.isEmpty(this.user)) {
			this.openLogin();
			return;
		}

		this.app.getRootNav().push(ShoppingcartPage);

	}

	viewChat() {
		if (_.isEmpty(this.user)) {
			this.openLogin();
			return;
		}
		let modal = this.modalCtrl.create(ChatPage);
		modal.present();
	}

	viewAccountSettings() {
		if (_.isEmpty(this.user)) {
			this.openLogin();
			return;
		}

		// this.navCtrl.push(AccountsettingsPage);
		this.app.getRootNav().push(AccountsettingsPage);
	}

	viewMyRating() {
		// this.navCtrl.push(MyratingPage);
		this.app.getRootNav().push(MyratingPage);
	}

	viewMyPurchases() {
		if (_.isEmpty(this.user)) {
			this.openLogin();
			return;
		}
		this.app.getRootNav().push(MypurchasesPage);
	}

	viewMyWallet() {
		// this.navCtrl.push(MywalletPage);
		this.app.getRootNav().push(MywalletPage);
	}

	viewAbout() {
		this.navCtrl.push(AboutPage);
	}

}



