import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import * as _ from 'lodash';

import { SearchresultPage } from '../../pages/searchresult/searchresult';

import { CategoriesServiceProvider } from '../../providers/service/categories';

@IonicPage()
@Component({
	selector: 'page-categories',
	templateUrl: 'categories.html',
})
export class CategoriesPage {
	categoriesArr: any[] = [];

	constructor(public navCtrl: NavController, public navParams: NavParams, public categories: CategoriesServiceProvider) {
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad CategoriesPage');
		this.categories.getAllCategories().then((data: any) => {
			console.log('categories: ', data);
			if (data && data.success) {
				this.categoriesArr = data.result;
				_.each(this.categoriesArr, (row: any) => {
					row.opened = false;
				});
			}
		});
	}

	doRefresh(ev) {
		console.log('ionViewDidLoad CategoriesPage');
		this.categories.getAllCategories().then((data: any) => {
			console.log('categories: ', data);
			if (data && data.success) {
				this.categoriesArr = data.result;
				ev.complete();
			}
		});
	}


	viewCategory(item){
		console.log('viewCategory');
		this.navCtrl.push(SearchresultPage);
	}


	viewSubcategory(item,$event) {
		console.log('viewSubcategory');
		_.each(this.categoriesArr, (row: any) => {
			if(item.categoryId == row.categoryId){
				row.opened = !row.opened;
			}
		});
		$event.stopPropagation();
	}

}



